import { Component, ViewChild, Renderer2, ElementRef, Output, EventEmitter, Input } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { icons } from '../../../icons';

@Component({
  selector: 'detail-component',
  templateUrl: './detail-component.html',
  styleUrls: ['detail-component.scss']
})
export class DetailComponent {
  @Input() item;
  @Input() thema;
  @Output() closeEvent = new EventEmitter();
  @ViewChild('content') content: ElementRef; 
  @ViewChild('mainIcon') mainIcon: ElementRef; 
  current;

  colors1 = [
    "#fe3f65",
    "#ffb645",
    "#fff289",
    "#a9e270",
    "#79a14b"
  ]
  colors2 = [
    "#37b8dd",
    "#5459eb",
    "#cf67ff",
    "#9b9b9b",
    "#000000"
  ]

  themaName;
  iconName;
  themaStyle;
  styleList;

  constructor(
    public router: Router,
    public renderer: Renderer2
  ) {}

  ngOnInit() {
    console.log("detail item test :: ", this.item)
    console.log("detail thema test :: ", this.thema)
    let themaList = this.thema.split('-');
    this.iconName = this.item.replace(/gonicon-/g, "");
    
    this.themaName = themaList[1];
    this.themaStyle = themaList[2];
    console.log(themaList)
    console.log(this.themaName);
    console.log(this.themaStyle);
    this.styleList = Object.keys(icons[this.themaName]);
  }

  ngAfterViewInit() {}

  changeColor(color, e){
    console.log("color :: ",color);
    console.log("e :: ",e);
    if(this.current){
      this.current.style.backgroundColor = "#FFFFFF";
    }
    this.mainIcon.nativeElement.style.color = color;
    e.target.style.backgroundColor = color;
    this.current = e.target;
  }

  dismiss(){
    Observable.of(1)
    .do(()=>{
      this.content.nativeElement.classList.remove('enter');
      this.content.nativeElement.classList.add('leave');
    })
    .delay(500)
    .subscribe(()=>{
      this.closeEvent.emit();
    })
  }

}
