import { Component, ViewChild, Renderer2, ElementRef } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { icons } from '../../icons';

@Component({
  selector: 'list-page',
  templateUrl: './list.html',
  styleUrls: ['list.scss']
})
export class ListPage {
  @ViewChild('bigNumber') bigNumber: ElementRef;
  @ViewChild('weatherLogo') weatherLogo: ElementRef;
  @ViewChild('nameB') nameB: ElementRef;
  @ViewChild('nameSky') nameSky: ElementRef;
  @ViewChild('infoWrapper') infoWrapper: ElementRef;
  @ViewChild('arrow') arrow: ElementRef;
  contentState = false;
  menuState = false;
  guideState = false;

  detailState = false;
  // detailState = true;
  // contentState = true;

  detailActiveItem = ""
  detailActiveThema = ""
  activeIcon:any = {
    name: "",
    list: []
  };


  constructor(
    public router: Router,
    public renderer: Renderer2
  ) {
    console.log("icons test :: ", icons)
  }

  ngOnInit() {
    this.changeActiveIcon("weather", "fill");
  }

  changeActiveIcon(name, thema){
    this.activeIcon.name = `gonicon-${name}-${thema}`;
    this.activeIcon.list = icons[name][thema];
  }

  ngAfterViewInit() {
    this.renderer.addClass(this.bigNumber.nativeElement, 'animated');
    this.renderer.addClass(this.weatherLogo.nativeElement, 'animated');
    this.renderer.addClass(this.nameB.nativeElement, 'animated');
    this.renderer.addClass(this.nameSky.nativeElement, 'animated');
    this.renderer.addClass(this.infoWrapper.nativeElement, 'animated');
  }

  menuOpen(e) {
    this.menuState = true;
    this.contentState = true;
  }
  menuClose(e) {
    this.menuState = false;
    this.contentState = false;
  }
  guideOpen(){
    if(this.arrow.nativeElement.classList.contains('active')) return false;

    Observable.of([''])
    .do(()=>{
      this.arrow.nativeElement.className += " active";
    })
    .delay(500)
    .subscribe(res => {
      this.arrow.nativeElement.className 
        = this.arrow.nativeElement.className.replace(/ active/g, "");
      this.guideState = true;
      this.contentState = true;
    })
  }
  guideClose(){
    this.guideState = false;
    this.contentState = false;
  }
  detailOpen(item){
    this.detailActiveItem = item;
    this.detailActiveThema = this.activeIcon.name;
    this.detailState = true;
    this.contentState = true;
  }
  detailClose(){
    this.detailState = false;
    this.contentState = false;
  }

}
